import React from 'react';
import { BrowserRouter, Switch, Route } from 'react-router-dom'
import Resume from './pages/Resume';
import Profile from './pages/Profile';

import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';

function App() {
  return (
    <BrowserRouter>
      <Switch>
        <Route path="/about">
          <Resume />
        </Route>
        <Route path="/" exact>
          <Profile />
        </Route>
      </Switch>
    </BrowserRouter>
  );
}

export default App;
